.RegisterLabel{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: .5rem;
}

.ltr {
    flex-direction: row;
}

.rtl {
    flex-direction: row-reverse;

    &:last-child{
        .input,.label{
            background-color: var(--color-pink);
            color: white;
        }
    }
}

.label{
    background-color: white;
    padding: .5rem;
}

.input {
    padding: .5rem 1rem;
    font-family: 'Courier New', Courier, monospace;
    font-size: 2rem;
    width: 10rem;
    text-align: center;
    border: none;
    box-shadow: 2px 3px 9px rgba($color: #000000, $alpha: .16);
    background-color: white;
    text-transform: uppercase;
}

.edit {
    width: 3rem;
    height: 3rem;
    margin: 0 .5rem;
    cursor: pointer;

    img {
        width: 100%;
        height: 100%;
    }
}