.Modal {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    padding: 2.5rem;
    background-color: white;
    border-radius: 1.5rem;

    &::before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: -10px;
        background-color: var(--color-pink);
        width: 100%;
        height: 3px;
    }
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input {
    margin: 2rem;
    border: 2px solid black;
    padding: 5px;
    text-align: center;
    font-size: 2rem;
}

.backDrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba($color: #000000, $alpha: .5);
}

.close {
    position: absolute;
    top: 0;
    right: 0;
    padding: .5rem;
    border: 2px solid var(--color-pink);;
    color: var(--color-pink);
    font-weight: bold;
    border-top-right-radius: 15px;
    cursor: pointer;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 2rem;
}