.Container {
  min-width: 110rem;
  min-height: 50rem;
  border-radius: 15px;
  box-shadow: 3px 3px 20px rgba(0, 0, 0,.16);

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);

  display: grid;
  grid-template-columns: 43% 1fr;
  grid-template-rows: 57% 43%;
  background: linear-gradient(90deg,rgba(#FFE1FB,0.85),rgba(#F9FFA8,0.95));
}

.header{
  font-family: sans-serif;
  letter-spacing: 2px;
  color: white;
  text-shadow: -2px 0 0 var(--color-pink), 0 -2px 0 var(--color-pink), 2px 0 0 var(--color-pink), 0 2px 0 var(--color-pink);
  position: absolute;
  top: -2rem;
  left: 3rem;
}

.title {
    opacity: .4;
    color: var(--color-pink);
    font-size: 5rem;
    font-family: 'Press Start 2P', cursive;
}

.Box{

  display: flex;
  align-items: center;
  justify-content: space-around;

  &--data{
    grid-column: 1 / 1;
    grid-row: 1 / 1;
    border-right: 2px dashed rgba($color: #000000, $alpha: .1);
    border-bottom: 2px dashed rgba($color: #000000, $alpha: .1);
    //background-color: #F2D335;
  }

  &--memory{
    grid-column: 2 / 3;
    grid-row: 1 / 3;
    //background-color: #F2E530;
  }

  &--stack {
    grid-column: 1/1;
    grid-row: 2/3;
    border-right: 2px dashed rgba($color: #000000, $alpha: .1);
    //background-color: #D9D9D9;
  }
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.center {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  justify-content: space-evenly;
}