.input {
    opacity: 0;
    position: fixed;
    width: 0;
}

.label {
    display: inline-block;
    padding: .5rem 1rem;
    margin-bottom: .5rem;
    font-size: 1.6rem;
    cursor: pointer;
    box-shadow: 2px 2px 10px 2px rgba(0,0,0,.4);
    background-color: white;
}

.input:checked + .label {
    background-color: var(--color-pink);
    color: white;
}